@charset "utf-8";

@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@100..900&display=swap');
body{
  font-family: 'Noto Sans KR', arial;
  color:#333;

}
h1{padding-left:20px;}
/* 메인서식 */
main{
  width:calc(100% - 20px);
  padding-inline:10px;
}
h2{
  padding-left:10px;
}
h3{
  padding-left:10px;
  font-size:14px;
  color:#aaa;
  font-weight: normal;
  padding-top: 10px;
}
.inputData{
  width:100%;
  padding-block:10px;
}
.input_inner{
  width:90%;
  border:1px solid #ddd;
  border-radius: 3px;
  margin:0 auto;
  padding:10px;
  
}
.inputData p{
  line-height: 200%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.inputData p label{
  display: inline-block;
  width:20%;
  text-align: right;
  margin-block:10px;
  @media screen and (max-width:540px) {
    width:22%;
    font-size:14px;
  }
}
.inputData p input[type="text"]{
  width:75%;
  height:30px;
  border:1px solid #ddd;
  padding-left: 8px;
  @media screen and (max-width:540px) {
    width:70%;
    font-size:14px;
  }
}
.inputData p:last-of-type input{
  display: inline-block;
  width:60px;
  height:30px;
  text-align: center;
  border:none;
  border-radius: 8px;
  background-color: #009c75;
  color:#fff;
  font-weight:600;
  margin-left:auto;
  margin-right:10px;
  cursor: pointer;
}
/* 리스트 */
.list{
  display: flex;
  flex-wrap: wrap;
  padding-inline: 10px;
  box-sizing: border-box;
  margin-bottom:100px;
}
.list li{
  width:calc(50% - 20px);
  list-style: none;
  border-radius: 20px;
  box-shadow: 0 0 2px rgba(0,0,0,.4);
  margin:10px;
}
.list li img{
  width:100%;
  height:150px;
  object-fit: cover;
  border-radius: 10px 10px 0 0;
}
.list li .detail{
  width:calc(100% - 20px);
  height:auto;
  padding-inline:10px;
  padding-bottom:10px;
}
.list li .detail .title{
  font-size:1rem;
  font-weight:600;
  margin-top:10px;
  line-height: 140%;
  @media screen and (max-width:540px) {
    font-size:14px;
  }
}
.list li .detail .price{
  text-align: right;
  color:#009c75;
  font-size:18px;
  font-weight:bold;
  margin-block:8px;
  @media screen and (max-width:540px) {
    font-size:16px;
  }
}
.list li .detail p:last-of-type{
  display: flex;
  justify-content: right;
  align-items: center;
  
}
.list li .detail p:last-of-type input{
  border:none;
  background-color: #ed4949;
  color:#fff;
  padding:5px 8px;
  border-radius: 5px;
  @media screen and (max-width:540px) {
    font-size:12px;
  }
}


footer{
  background-color: #333;
  color:#fff;
  text-align: center;
  width:100%;
  height:50px;
  display: flex;
  align-items: center;
  justify-content: center;
}

